*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat';
  /* overflow: hidden; */
}
.food-heading h1{
  font-family: 'Montserrat';
  font-weight: bold;
}

.heading{
  text-align: center;
}

.input-section{
  display: flex;
  align-items: center;justify-content: center;
}

.content{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
}

.content_{
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  padding: 80px 0px;
}

.max-width{
  margin: 0 auto ;
  max-width: 1265px;
}

.card-title{

  font-weight: bold;
  font-size: 16px;
}

.card-text{

  font-size: 14px;
}

.intro-head{
  margin-top: 80px;;
}

.intro-head h1{
  font-weight: 100;
}

.recipe-detail{
  padding: 10px !important;
}

.ing-btn{
  margin-bottom: 20px;
}

.footerStyle{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-around;
  height: 6vh;
  background-color: black;
}

.footerStyle p{
  color: white;
  font-size: 11px;
  margin-bottom: 0px;
}